<template>
  <v-container>
    <div class="Echarts">
      <div id="main" style="width: 50vw; height: 50vh"></div>
    </div>
  </v-container>
</template>

<script>
/* eslint-disable */

import * as echarts from "echarts";

export default {
  name: "charts",
  props: ["odata"],
  data() {
    return {
      chartData: null,
    };
  },
  mounted() {
    this.refresh();
  },
  methods: {
    refresh() {
      var sum = {};
      this.odata.forEach((item) => {
        // sum[item.detectobject] += 1;
        item.items.forEach((i) => {
          console.log(sum[i.detectobject]);
          if (sum[i.detectobject] == null) {
            sum[i.detectobject] = 0;
          }
          sum[i.detectobject] += 1;
        });
      });
      this.chartData = sum;
      this.myEcharts();
    },
    myEcharts() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(document.getElementById("main"));

      // 指定图表的配置项和数据
      var option = {
        title: {
          text: "病害统计",
        },
        tooltip: {},
        legend: {
          data: ["数量"],
        },
        xAxis: {
          data: Object.keys(this.chartData),
        },
        yAxis: {
          name: "数量",
        },
        series: [
          {
            name: "数量",
            type: "bar",
            data: Object.values(this.chartData),
          },
        ],
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    },
  },
  watch: {
    odata: {
      handler() {
        this.refresh();
      },
      deep: true,
    },
  },
};
</script>

