<template>
  <v-card>
    <div class="d-flex">
      <div style="width: 40vw; height: 100%; margin: 20px">
        <p style="text-align: left; font-size: 20px">导出设置</p>

        <v-btn color="blue" @click="exportXls" dark> 导出为excel </v-btn>
      </div>
      <div style="padding: 20px; margin: 20px; height: 90vh;overflow-y: auto;">
        <p style="text-align: center; font-size: 30px; font-weight: bold">
          报告预览
        </p>

        <table  class="table1" style="width: 40vw;h">
          <tbody>
            <th colspan="5" style="text-align: center; font-size: 30px; ">
              水稻病害检测报告
            </th>
            <tr>
              <td>导出日期</td>
              <td colspan="2">{{ report_tm }}</td>
              <td>报告人</td>
              <td>{{ user.name }}</td>
            </tr>
            <tr>
              <td colspan="5" style="text-align: left; padding-left: 15px">
                批次信息
              </td>
            </tr>
            <tr>
              <td>序号</td>
              <td colspan="3">检测日期</td>
              <td>数量(张)</td>
            </tr>
            <tr v-for="(item, i) in recentData" :key="JSON.stringify(item)">
              <td>{{ i }}</td>
              <td colspan="3">{{ item.tm }}</td>
              <td>{{ item.results.length }}</td>
            </tr>
            <tr>
              <td colspan="5" style="text-align: left; padding-left: 15px">
                详细信息
              </td>
            </tr>
            <tr>
              <td>批次</td>
              <td>图片序号</td>
              <td>病害类型</td>
              <td>病害位置</td>
              <td>病害大小</td>
            </tr>
            <tr v-for="(item, i) in reformatedData" :key="i">
              <td>{{ item.batch }}</td>
              <td>{{ item.imgIndex }}</td>
              <td>{{ item.detectobject }} score:{{ item.score }}</td>
              <td>x:{{ item.locationx }} y:{{ item.locationy }}</td>
              <td>{{ item.width }} x {{ item.height }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </v-card>
</template>

<script>
/* eslint-disable */

export default {
  name: "reporter",
  props: ["user", "recentData", "report_tm"],
  data() {
    return {};
  },
  methods: {
    exportXls() {
      debugger;
      let tableHtml = document.getElementsByClassName("table1");
      var appendHtml = "";
      for (var i = 0; i < tableHtml.length; i++) {
        appendHtml += tableHtml[i].outerHTML;
      }
      let html =
        "<html><head><meta charset='utf-8' /></head><body>" +
        appendHtml +
        "</body></html>";
      // 实例化一个Blob对象，其构造函数的第一个参数是包含文件内容的数组，第二个参数是包含文件类型属性的对象
      let blob = new Blob([html], { type: "application/vnd.ms-excel" }); //application/octet-stream
      //也可以用js创建一个a标签
      let a = document.createElement("a");
      // var a = document.getElementsByTagName("a")[0];
      // 利用URL.createObjectURL()方法为a元素生成blob URL
      a.href = URL.createObjectURL(blob);
      // 设置文件名
      a.click();
    },
  },
  computed: {
    reformatedData() {
      var data = [];
      this.recentData.forEach((item, i) => {
        item.results.forEach((a, imgIndex) => {
          a.items.forEach((b) => {
            data.push({
              batch: i,
              imgIndex: imgIndex,
              detectobject: b.detectobject,
              score: b.score,
              locationx: b.locationx,
              locationy: b.locationy,
              width: b.width,
              height: b.height,
            });
          });
        });
      });

      return data;
    },
  },
};
</script>

<style>
table,
tr td {
  border: 1px solid gray;
  text-align: center;
  margin: auto;
}
table {
  border-collapse: collapse;
}

/* * {
  font-family: "宋体";
} */
</style>