<template>
  <div style="margin-left: 10px">
    <v-card
      height="90vh"
      width="80vw"
      flat
      style="padding: 10px; overflow: auto"
    >
      <v-card-title>使用指南:</v-card-title>
      <v-card-text>
        <v-stepper v-model="e6" vertical flat>
          <v-stepper-step :complete="e6 > 1" step="1">
            选择需要检测的图片
            <small>可以选择多张图片</small>
          </v-stepper-step>

          <v-stepper-content step="1">
            <v-card color="grey lighten-1" class="mb-12" height="600px" width="800px">
              <img src="img/step1.png"
            /></v-card>
            <v-btn color="primary" @click="e6 = 2">下一步</v-btn>
            <v-btn text @click="e6 -= 1">上一步</v-btn>
          </v-stepper-content>

          <v-stepper-step :complete="e6 > 2" step="2"
            >点击开始识别并等待到进度条完成</v-stepper-step
          >

          <v-stepper-content step="2">
            <v-card color="grey lighten-1" class="mb-12" height="600px" width="800px">
              <img src="img/step2.png"
            /></v-card>
            <v-btn color="primary" @click="e6 = 3">下一步</v-btn>
            <v-btn text @click="e6 -= 1">上一步</v-btn>
          </v-stepper-content>

          <v-stepper-step :complete="e6 > 3" step="3"
            >从对话框左侧的列表里选择要查看的项目</v-stepper-step
          >

          <v-stepper-content step="3">
            <v-card
              color="grey lighten-1"
              class="mb-12"
              height="600px"
            ></v-card>
            <v-btn color="primary" @click="e6 = 4">下一步</v-btn>
            <v-btn text @click="e6 -= 1">上一步</v-btn>
          </v-stepper-content>

          <v-stepper-step step="4"
            >点击统计选项卡可以获取本次识别的病害统计结果</v-stepper-step
          >
          <v-stepper-content step="4">
            <v-card
              color="grey lighten-1"
              class="mb-12"
              height="600px"
            ></v-card>
            <v-btn color="primary" @click="e6 = 1">下一步</v-btn>
            <v-btn text @click="e6 -= 1">上一步</v-btn>
          </v-stepper-content>
        </v-stepper>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "UserGuide",
  data() {
    return {
      e6: 1,
    };
  },
};
</script>

