<script>
/**************************************时间格式化处理************************************/

/* eslint-disable */
function rmblank(str) {
  return str.replace(/\s*/g, "");
}
function check(str) {
  return str.length >= 2;
}
function dateFtt(fmt, date) {
  //author: meizz
  var o = {
    "M+": date.getMonth() + 1, //月份
    "d+": date.getDate(), //日
    "h+": date.getHours(), //小时
    "m+": date.getMinutes(), //分
    "s+": date.getSeconds(), //秒
    "q+": Math.floor((date.getMonth() + 3) / 3), //季度
    S: date.getMilliseconds(), //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
}
import axios from "axios";
import Charts from "./components/Charts.vue";
import Help from "./components/Help.vue";
import reporter from "./components/Report.vue";
export default {
  name: "App",
  components: { Charts, Help, reporter },
  data: () => ({
    user: {
      name: "admin",
    },
    report_tm: "",
    showReport: false,
    snackbar: false,
    text: "",
    navIndex: 0,
    recent: false,

    selectedRecentItem: null,

    selectedItem: 0,

    detectlog: "",

    recentData: [],
    progress: {
      total: 0,
      finished: 0,
    },
    valid: true,
    loading: false,

    keepValue: true,

    allResults: [],
    // dialog: false,
    dialog: false,
    fileInfo: [],
    rules: {
      notempty: [(v) => !!v || "Item is required"],
    },
  }),
  methods: {
    detect(img) {
      var timer = [];
      timer[0] = new Date().getTime();

      let param = new FormData(); //创建form对象
      param.append("image", img); //通过append向form对象添加数据
      console.log(param.get("image")); //FormData私有类对象
      let config = {
        headers: { "Content-Type": "multipart/form-data" },
      };
      debugger;
      axios
        .post("detect", param, config)
        .then((response) => {
          var result = {
            items: [],
            tm_cost: 0,
            img_url: "",
            log: "",
          };
          timer[1] = new Date().getTime();
          var results = response.data.info.split("\n").filter(check);
          result.log = response.data;
          result.img_url = response.data.url + ".jpg";
          result.tm_cost = (timer[1] - timer[0]) / 1000;
          var _this = this;
          results.forEach((value) => {
            result.items.push(_this.serialize(value));
          });
          this.allResults.push(result);
          this.progress.finished += 1;
          if (this.progressValue == 100) {
            this.addRecent(this.allResults);
            this.dialog = true;
            this.loading = false;
          } else {
            this.detect(this.fileInfo[this.progress.finished]);
          }
        })
        .catch((err) => {
          console.error(err);
          this.myalert(err);
          this.loading = false;
        });
    },
    setcfg() {
      this.loading = true;
      this.progress = {
        total: 0,
        finished: 0,
      };
      if (this.fileInfo.length <= 0) {
        this.myalert("请选择至少一张图片！");
        this.loading = false;
        return;
      }
      // if (!this.keepValue) {
      this.allResults = [];
      // }
      this.progress.total = this.fileInfo.length;

      this.detect(this.fileInfo[this.progress.finished]);
    },
    clean() {
      axios
        .get("clean")
        .then((res) => {
          this.myalert(res.data);
        })
        .catch();
    },
    myalert(msg) {
      this.text = msg;
      this.snackbar = true;
    },
    serialize(str) {
      var lists = str.split(",").filter(check);
      var r = {};
      // var _this = this;
      lists.forEach((a) => {
        // console.log(index, array);
        if (a == " detection") return true;
        else {
          console.log(a);
          a = rmblank(a);
          var value = a.split(":");
          r[value[0]] = value[1];
        }
      });
      return r;
      // for (var info in lists) {
      //   if (info == " detection") continue;
      //   else {
      //     var key,
      //       value = info.split("=");
      //     r[key] = value;
      //   }
      // }
      // return r;
    },
    showRecent(index) {
      this.allResults = this.recentData[index].results;
      this.dialog = true;
    },
    addRecent(results) {
      var d = new Date(); //获取系统当前时间
      var tm = dateFtt("yyyy-MM-dd hh:mm:ss", d);
      var obj = {
        tm: tm,
        results: results,
      };
      this.recentData.push(obj);
    },
    clearRecent() {
      this.recentData = [];
      this.myalert("清除成功！");
    },
    showReportView() {
      this.showReport = true;
      var d = new Date(); //获取系统当前时间
      this.report_tm = dateFtt("yyyy-MM-dd hh:mm:ss", d);
    },
  },
  computed: {
    progressValue() {
      if (this.total == 0) {
        return 0;
      } else {
        return (this.progress.finished / this.progress.total) * 100;
      }
    },
    level(){
      var obj={
        //label
        "disease":"B",
      }
      var rL=[];
      var r="";
      try{
        debugger;
        if(this.allResults[this.selectedItem].items==undefined){
          return "Undefined";
        }
        this.allResults[this.selectedItem].items.forEach(element => {
          rL.push(element.detectobject)
        });
        for (var ele in Object.keys(obj)){
          if(rL.indexOf(Object.keys(obj)[ele])!=-1){
            r= obj[Object.keys(obj)[ele]];
            break;
          }
        }
        if(r!=""){
          return r
        }else{
          return "A";
        }
      }
      catch{
        return "Unknown";
      }
    }
  },
};
</script>
